import React from "react";
import InteractiveMotif from "../pattern/InteractiveMotif";
import { Motif } from "../utils/types";

interface Props {
  motifs: Motif[];
}

export default function RepeatedMotifs({ motifs }: Props) {
  return (
    <div>
      <div>
        Stitch patterns are interactive. Click symbols in the chart to highlight
        the corresponding written instruction.
      </div>
      {motifs.map((motif, i) => (
        <InteractiveMotif key={i} {...motif} />
      ))}
    </div>
  );
}
