import { css, cx } from "@linaria/core";
import React from "react";

interface Props {
  menus: string[];
  selectedMenu?: any;
  setMenu: (arg0: any) => void;
  type?: "primary" | "secondary" | "tertiery";
}

const styles = {
  menuWrapper: css`
    margin-bottom: 32px;
  `,
  menu: css`
    text-decoration: underline;
    cursor: pointer;
    &:not(:last-child) {
      margin-right: 16px;
    }
  `,
  selectedMenu: css`
    font-weight: 600;
    text-decoration: none;
  `,
};

export default function Nav({
  menus,
  selectedMenu,
  setMenu,
  type = "primary",
}: Props) {
  return (
    <div className={styles.menuWrapper}>
      {menus.map((d, i) => (
        <span
          key={i}
          className={cx(styles.menu, selectedMenu === i && styles.selectedMenu)}
          role="button"
          onClick={() => setMenu(i)}
        >
          {d}
        </span>
      ))}
    </div>
  );
}
