import { Motif } from "../../utils/types";
import { getChartFromText, getFlippedChart } from "../../utils/calculator";

const TITLE = "Basic Braid";

export const getPattern = (stsCount: 9 | 6 | 3, rowCount: 8 | 6 | 4): Motif => {
  const rightCable = stsCount === 3 ? "c2r" : stsCount === 6 ? "c4r" : "c6r";

  const base = getChartFromText([
    `k:${stsCount} * ${rowCount / 2 - 2}`,
    `k:${stsCount / 3} ${rightCable}`,
    `k:${stsCount}`,
  ]);

  const flippedBase = getFlippedChart(base);

  const pattern = [...base, ...flippedBase];

  return {
    title: TITLE,
    chart: pattern,
    stsCount,
  };
};
