import { SizeDetail } from "../../projects/utils/types";
import stitchDefinition from "../../stitches/definition";
import {
  getBaseChartFromRawChart,
  getChartFromBasePattern,
  getPushedMotifList,
  getRawChartFromBaseChart,
  getSlicedBasePattern,
} from "../../utils/calculator";
import {
  BaseChart,
  BasePattern,
  Chart,
  MotifInPattern,
  RawChart,
  Stitch,
} from "../../utils/types";
import {
  getChartFromRawChart,
  getFullLengthBaseChart,
  getIncEveryXandYRows,
  rounded,
  countFromGuage,
  stsFromRow,
  getValidRawSts,
} from "./common";
import { getCurveSRCount, getLineSRCount, getSRChart } from "./shortRow";
const { koco, rli, lli, k, empty, p, k2tog, ssk } = stitchDefinition;

function getIncEveryXRows(
  baseChart: BaseChart,
  startRowID: number,
  rowCount: number,
  byEveryXRow: number,
  knitPaddingCount: number = 0
): BaseChart {
  let incRowID = startRowID;
  let sideKnitCount = knitPaddingCount;
  const rows: BaseChart = [];
  for (let i = 0; i < rowCount; i++) {
    const row = baseChart.slice(incRowID, incRowID + 1).flat();
    row.unshift(...Array(sideKnitCount).fill(k));
    row.push(...Array(sideKnitCount).fill(k));
    row.splice(1, 1, rli);
    row.splice(row.length - 2, 1, lli);
    rows.push(row);
    incRowID++;
    sideKnitCount++;
    // Additional rows after M1 on both sides, add K
    for (let j = 1; j < byEveryXRow; j++) {
      const row = baseChart.slice(incRowID, incRowID + 1).flat();
      row.unshift(...Array(sideKnitCount).fill(k));
      row.push(...Array(sideKnitCount).fill(k));
      rows.push(row);
      incRowID++;
    }
  }
  return rows;
}

function getDecEveryXRows(
  baseChart: BaseChart,
  startRowID: number,
  decCount: number,
  byEveryXRow: number,
  emptyPaddingCount: number = 0
): BaseChart {
  let decRowID = startRowID;
  let sideEmptyCount = emptyPaddingCount;
  const rows: RawChart = [];
  const getRawRow = (i: number) =>
    getRawChartFromBaseChart(baseChart.slice(i, i + 1))[0];
  for (let i = 0; i < decCount; i++) {
    const rawRow = getRawRow(decRowID);
    const replacedSts = 2 + sideEmptyCount;
    const nonEmptyStsCount = rawRow.length - replacedSts * 2;
    const row = getValidRawSts(
      rawRow,
      replacedSts,
      replacedSts + nonEmptyStsCount
    );
    const emptySts = Array(sideEmptyCount).fill("empty");
    row.splice(0, replacedSts, ...emptySts, "empty", "k2tog");
    row.splice(
      row.length - replacedSts,
      replacedSts,
      "ssk",
      "empty",
      ...emptySts
    );
    rows.push(row);
    decRowID++;
    sideEmptyCount++;
    // Additional rows after descrease on both sides, add empty
    for (let j = 1; j < byEveryXRow; j++) {
      const rawRow = getRawRow(decRowID);
      const nonEmptyStsCount = rawRow.length - sideEmptyCount * 2;
      const row = getValidRawSts(
        rawRow,
        sideEmptyCount,
        sideEmptyCount + nonEmptyStsCount
      );
      const emptySts = Array(sideEmptyCount).fill("empty");
      if (row == null) {
        break;
      }
      row.splice(0, sideEmptyCount, ...emptySts);
      row.splice(row.length - sideEmptyCount, sideEmptyCount, ...emptySts);
      rows.push(row);
      decRowID++;
    }
  }
  return getBaseChartFromRawChart(rows);
}

function getCoIncRows(
  baseChart: BaseChart,
  startRowID: number,
  coIncCount: number,
  prevIncCount: number = 0
): BaseChart {
  const incRows = baseChart
    .slice(startRowID, startRowID + 2)
    .map((row) => row.flat());
  // Right side - add CO at the end
  incRows[0].unshift(
    ...Array(coIncCount).fill(empty),
    ...Array(prevIncCount).fill(k)
  );
  incRows[0].push(
    ...Array(prevIncCount).fill(k),
    ...Array(coIncCount).fill(koco)
  );
  // Wrong side - add CO at the end
  incRows[1].push(...Array(prevIncCount + coIncCount).fill(k));
  incRows[1].unshift(
    ...Array(coIncCount).fill(koco),
    ...Array(prevIncCount).fill(k)
  );
  return incRows;
}

function getCenteredBasePattern(
  baseChart: BaseChart,
  motifList: MotifInPattern[],
  maxStsCount: number = 0,
  minStsCount: number = 0
): BasePattern {
  const centeredBaseChart = baseChart.map((row) => {
    const emptyCount = (maxStsCount - row.length) / 2;
    const emptySts = Array(emptyCount).fill(empty);
    return [...emptySts, ...row, ...emptySts];
  });
  return {
    chart: centeredBaseChart,
    motifList: getPushedMotifList(motifList, (maxStsCount - minStsCount) / 2),
  };
}

export function getUnderArmBaseChart(
  baseChartUnit: BaseChart,
  type: "even" | "odd",
  armInsetSts: number,
  mergeSts: number = 0
): BaseChart {
  let stsCount = (armInsetSts + mergeSts) * 2;
  if (
    (stsCount % 2 === 0 && type === "odd") ||
    (stsCount % 2 === 1 && type === "even")
  ) {
    stsCount -= 1;
  }
  const baseChart = baseChartUnit.map((row) => {
    return Array.from({ length: stsCount }).map((_, i) => row[i % row.length]);
  });
  return baseChart;
}

function getCardiganBody(
  basePattern: BasePattern,
  startRowID: number,
  totalRow: number,
  waistRow: number,
  underArmChart: BaseChart,
  coSts: number,
  motifStartRowID: number,
  centerKSts: number = 0,
  underArmMergeSts: number = 0
): Chart {
  const baseStsCount = basePattern.chart[0].length;
  const totalLengthRow = totalRow - waistRow;
  const leftFront = getSlicedBasePattern(
    basePattern,
    [Math.ceil(baseStsCount / 2), baseStsCount - underArmMergeSts],
    [startRowID, totalLengthRow],
    -(startRowID % basePattern.chart.length),
    centerKSts
  );
  const back = getSlicedBasePattern(
    basePattern,
    [underArmMergeSts, baseStsCount - underArmMergeSts],
    [startRowID, totalLengthRow],
    -(startRowID % basePattern.chart.length)
  );
  const rightFront = getSlicedBasePattern(
    basePattern,
    [underArmMergeSts, Math.floor(baseStsCount / 2)],
    [startRowID, totalLengthRow],
    -(startRowID % basePattern.chart.length),
    -centerKSts
  );

  const decStsIDs: number[] = [];
  const body = Array.from({ length: totalLengthRow - startRowID }).map(
    (_, i) => {
      const underArm = [...underArmChart[i % underArmChart.length]];
      // Add CO in the first row
      if (i === 0) {
        underArm.splice(
          Math.round((underArm.length - coSts) / 2),
          coSts,
          ...Array(coSts).fill(koco)
        );
      }
      const row = [
        ...leftFront.chart[i],
        ...underArm,
        ...back.chart[i],
        ...underArm,
        ...rightFront.chart[i],
      ];

      // Decrease last sts
      if (i === totalLengthRow - startRowID - 1) {
        const rawSts = getRawChartFromBaseChart([row], true)[0];
        // Decrease sts up to 90%, make it odd number for k1p1 rib
        const totalSts = row.length;
        const totalDecSts =
          totalSts - (Math.ceil((totalSts * 0.9) / 2) * 2 + 1);
        const everyX = Math.floor(totalSts / totalDecSts);
        // Decrease start stitch number, then decrease 1 sts at everyX sts
        let curDecSts = Math.round((totalSts - everyX * (totalDecSts - 1)) / 2);
        while (curDecSts < totalSts) {
          rawSts.splice(
            curDecSts,
            1,
            rawSts[curDecSts] === "p" ? "p2tog" : "k2tog"
          );
          decStsIDs.push(curDecSts);
          curDecSts += everyX;
        }
        return getBaseChartFromRawChart([rawSts])[0];
      }
      return row;
    }
  );

  // Rib
  const rib = Array.from({ length: waistRow }).map((_) => {
    const stsCount = body[body.length - 1].length;
    let emptyCount = 0;
    return Array.from({ length: stsCount }).map((_, i) => {
      if (i < 2 || i > stsCount - 2) {
        return k;
      } else if (decStsIDs.includes(i - 1)) {
        emptyCount++;
        return empty;
      } else {
        return (i - emptyCount) % 2 === 0 ? p : k;
      }
    });
  });

  // Motif list
  const armInsetSts = underArmChart[0].length - underArmMergeSts;
  const backMotifList = getPushedMotifList(
    back.motifList,
    baseStsCount / 2 + armInsetSts
  );
  const rightFrontMotifList = getPushedMotifList(
    rightFront.motifList,
    baseStsCount / 2 + baseStsCount + armInsetSts * 2 - underArmMergeSts
  );
  return getChartFromBasePattern({
    chart: [...body, ...rib],
    motifList: [
      ...leftFront.motifList,
      ...backMotifList,
      ...rightFrontMotifList,
    ],
    motifStartRowID,
  });
}

type SetInSleeveSize = {
  shortRow: { [k: string]: number[] };
  row: { [k: string]: number };
  sts: { [k: string]: number };
};

export function getSetInSleeveSize(
  size: SizeDetail,
  stsGuage: number,
  rowGuage: number,
  stretchRatio: number = 1
): SetInSleeveSize {
  // sizes
  const { length, depth, width, band, circumference } = size;
  const { neck, shoulder, armInset } = width;
  const { waist, neck: neckBand, button, wrist } = band;
  const { total, shoulderToSleeve, sleeve } = length;
  const {
    neckBack,
    shoulderDrop,
    armInset: insetHeight,
    shoulderToArm,
    neckFront,
  } = depth;

  const totalRow = countFromGuage(total, rowGuage);

  // Back neck depth
  const neckSts = countFromGuage(neck, stsGuage);
  const neckBackRow = countFromGuage(neckBack, rowGuage, 2);
  // Shoulder drop
  const shoulderSts = countFromGuage(shoulder, stsGuage);
  const shoulderDropRow = countFromGuage(shoulderDrop, rowGuage, 2);

  // Should to arm bottom - before arm increase, rows with same sts count, must end on wrong side (even number rows)
  const shoulderToArmInsetRow = countFromGuage(
    shoulderToArm - neckBack - insetHeight,
    rowGuage,
    2
  );
  const armInsetSts = countFromGuage(armInset, stsGuage);
  const armInsetRow = countFromGuage(insetHeight, rowGuage, 2);

  // Front
  const neckRow = countFromGuage(neckFront, rowGuage, 2) + 2;

  // Whole body
  const armToWaistRow = countFromGuage(
    total - shoulderToArm - waist,
    rowGuage,
    2
  );

  /** short rows */
  // sts around the back neck line; from the top neck side to bottom center
  // Per every other row (y) get sts count (x) from the center (0, 0)
  const backNeckCurve = getCurveSRCount(Math.round(neckSts / 5), neckBackRow);
  const backShoulderLine = getLineSRCount(shoulderSts, shoulderDropRow);

  /** Increase on Arm */
  const armCoSts = rounded(armInsetSts / 3);
  const armIncSts = armInsetSts - armCoSts * 2;
  const { everyX: every4, everyY: every2 } = getIncEveryXandYRows(
    armInsetRow - 2,
    armIncSts,
    4,
    2
  );

  /** Front short rows */
  const halfNeckSts = Math.floor(neckSts / 2);
  const curveBaseSts = Math.floor(neckSts / 5.5);
  const curveOffsetRow = rounded(shoulderDropRow * 1, 2);
  const frontNeckCurveBase = getCurveSRCount(
    curveBaseSts,
    neckRow - curveOffsetRow
  );
  const frontNeckCurveExtended = Array(curveOffsetRow / 2)
    .fill(curveBaseSts)
    .concat(...frontNeckCurveBase)
    .map((s) => s + (halfNeckSts - curveBaseSts));
  // Find SR starting row, replace empty to 0
  const frontNeckCurve = frontNeckCurveExtended.map((s, i) =>
    i < frontNeckCurveExtended.length - 2 &&
    i > 0 &&
    s === frontNeckCurveExtended[i - 1] &&
    s > frontNeckCurveExtended[i + 1]
      ? 0
      : s
  );

  /** Arm */
  const startArmSts = Math.round(
    stsFromRow(shoulderToArmInsetRow * 2, stsGuage, rowGuage) * stretchRatio
  );
  const srArmSts =
    Math.round(
      Math.sqrt(
        Math.pow(every4 * 4 + every2 * 2, 2) +
          Math.pow(stsFromRow(armInsetRow - 2, stsGuage, rowGuage), 2)
      )
    ) * 2;
  const minArmSts = startArmSts + srArmSts + armInsetSts * 2;

  return {
    row: {
      totalRow,
      neckBackRow,
      shoulderDropRow,
      shoulderToArmInsetRow,
      armInsetRow,
      armToWaistRow,
      neckRow,
      every4,
      every2,
      shoulderToSleeveRow: countFromGuage(shoulderToSleeve, rowGuage),
      sleeveRow: countFromGuage(sleeve, rowGuage),
      waistBandRow: countFromGuage(waist, rowGuage, 2),
      armBandRow: countFromGuage(neckBand, rowGuage, 2),
      buttonBandRow: countFromGuage(button, rowGuage, 2),
      wristBandRow: countFromGuage(wrist, rowGuage, 2),
    },
    sts: {
      neckSts,
      halfNeckSts,
      shoulderSts,
      armInsetSts,
      armCoSts,
      armIncSts,
      srArmSts,
      minArmSts,
      wristSts: countFromGuage(circumference.wrist, stsGuage, 2),
    },
    shortRow: {
      backNeckCurve,
      backShoulderLine,
      frontNeckCurve,
    },
  };
}

export function getSetInSleeveCardigan(
  size: SetInSleeveSize,
  basePattern: BasePattern,
  underArmChart: BaseChart,
  underArmMergeSts: number = 0,
  centerKSts: number = 0
): Chart[] {
  // sizes
  const { row, sts, shortRow } = size;
  const {
    totalRow,
    neckBackRow,
    shoulderDropRow,
    shoulderToArmInsetRow,
    waistBandRow,
    neckRow,
    every4,
    every2,
  } = row;
  const { shoulderSts, armCoSts, armIncSts } = sts;

  const { backNeckCurve, backShoulderLine, frontNeckCurve } = shortRow;

  // Repeats of base pattern upto full garment length
  let rowCutID = Math.max(neckBackRow, shoulderDropRow);
  const fullLengthBaseChart: BaseChart = getFullLengthBaseChart(
    basePattern.chart,
    totalRow
  );

  // Back top right (part1) & Back top left (part2)
  const backTopSideSts = shoulderSts + backNeckCurve[0];
  const part1Chart = getSRChart(
    basePattern,
    -backTopSideSts,
    0,
    rowCutID,
    backNeckCurve,
    [0].concat(backShoulderLine),
    true,
    false
  );
  const part2Chart = getSRChart(
    basePattern,
    backTopSideSts,
    0,
    rowCutID,
    [0].concat(backShoulderLine),
    backNeckCurve,
    false,
    true
  );

  // Back; shoulder to arm bottom
  const backTopConnectRow = fullLengthBaseChart
    .slice(neckBackRow, neckBackRow + 1)
    .flat();
  const kocoCount = backTopConnectRow.length - backTopSideSts * 2;
  // backNeckCurve[backNeckCurve.length - 1];
  backTopConnectRow.splice(
    backTopSideSts,
    kocoCount,
    ...Array(kocoCount).fill(koco)
  );
  const backRepeats = [backTopConnectRow].concat(
    fullLengthBaseChart.slice(rowCutID + 1, rowCutID + shoulderToArmInsetRow)
  );
  rowCutID += shoulderToArmInsetRow;

  const every4Inc = getIncEveryXRows(fullLengthBaseChart, rowCutID, every4, 4);
  rowCutID += 4 * every4;
  const every2Inc = getIncEveryXRows(
    fullLengthBaseChart,
    rowCutID,
    every2,
    2,
    every4
  );
  rowCutID += 2 * every2;
  const coInc = getCoIncRows(
    fullLengthBaseChart,
    rowCutID,
    armCoSts,
    every4 + every2
  );
  const part3BaseChart = [...backRepeats, ...every4Inc, ...every2Inc, ...coInc];
  const centeredPart3BasePattern = getCenteredBasePattern(
    part3BaseChart,
    basePattern.motifList,
    coInc[0].length,
    backTopConnectRow.length
  );

  const part3Chart = getChartFromBasePattern({
    ...centeredPart3BasePattern,
    motifStartRowID: basePattern.chart.length - neckBackRow,
  });
  rowCutID += 2;

  // front
  const neckTopSideSts = basePattern.chart[0].length / 2;

  // right
  const rightFrontSRChart = getSRChart(
    { chart: fullLengthBaseChart, motifList: basePattern.motifList },
    neckTopSideSts,
    0,
    neckRow,
    [0].concat(backShoulderLine),
    frontNeckCurve
  ).map((row) => {
    return [{ sts: Array(armIncSts + armCoSts).fill(empty) }].concat(...row);
  });

  const backSts = coInc[0].length;
  const rightFrontRest = getSlicedBasePattern(
    centeredPart3BasePattern,
    [0, Math.floor(backSts / 2)],
    [
      neckRow - part1Chart.length,
      neckRow - part1Chart.length + shoulderToArmInsetRow,
    ],
    -(neckRow % basePattern.chart.length),
    -centerKSts
  );
  const part4Chart = [
    ...rightFrontSRChart,
    ...getChartFromBasePattern(rightFrontRest),
  ];

  // left
  const leftFrontSRChart = getSRChart(
    { chart: fullLengthBaseChart, motifList: basePattern.motifList },
    -neckTopSideSts,
    0,
    neckRow,
    frontNeckCurve,
    [0].concat(backShoulderLine)
  ).map((row) => {
    return row.concat({ sts: Array(armIncSts + armCoSts).fill(empty) });
  });

  const leftFrontRest = getSlicedBasePattern(
    centeredPart3BasePattern,
    [Math.ceil(backSts / 2), backSts],
    [
      neckRow - part1Chart.length,
      neckRow - part1Chart.length + shoulderToArmInsetRow,
    ],
    -(neckRow % basePattern.chart.length),
    centerKSts
  );
  const part5Chart = [
    ...leftFrontSRChart,
    ...getChartFromBasePattern(leftFrontRest),
  ];

  const bodyConnect = getCardiganBody(
    { chart: fullLengthBaseChart, motifList: basePattern.motifList },
    rowCutID,
    totalRow,
    waistBandRow,
    underArmChart,
    underArmChart[0].length - (armCoSts + armIncSts + underArmMergeSts) * 2,
    basePattern.chart.length - (rowCutID % basePattern.chart.length),
    centerKSts,
    underArmMergeSts
  );

  return [
    part1Chart,
    part2Chart,
    part3Chart,
    part4Chart,
    part5Chart,
    bodyConnect,
  ];
}

export function getSetInSleeveArm(
  size: SetInSleeveSize,
  armPattern: BasePattern
): Chart {
  // Base sleeve pattern
  const { sts, row } = size;
  const { srArmSts, minArmSts, wristSts } = sts;
  const { shoulderToSleeveRow, sleeveRow, wristBandRow } = row;
  const baseStsCount = armPattern.chart[0].length;
  const kSide = rounded(minArmSts - baseStsCount, 2) / 2;
  const padding: Stitch[] = Array.from({ length: kSide }).map((_) => k);
  const armBaseChart: BaseChart = getFullLengthBaseChart(
    armPattern.chart.map((row) => {
      return [...padding, ...row, ...padding];
    }),
    shoulderToSleeveRow + sleeveRow + wristBandRow
  );

  // Short Row along the curved increase, flip the round curve
  const curveSR = getCurveSRCount(srArmSts, shoulderToSleeveRow / 2)
    .reverse()
    .map((s) => Math.min(srArmSts, srArmSts - s))
    .filter((s) => s > 0);
  // Make the sleeve line more diagonal
  const lineSR = getLineSRCount(srArmSts, shoulderToSleeveRow / 2);
  let rowCutID = curveSR.length * 2 + 1 + lineSR.length * 2 + 1;
  const totalSts = armBaseChart[0].length;
  const sleeveMotifList = getPushedMotifList(armPattern.motifList, kSide);
  const sleeveSRChart = getSRChart(
    {
      chart: armBaseChart,
      motifList: sleeveMotifList,
    },
    totalSts,
    0,
    rowCutID,
    [0, ...curveSR, 0, ...lineSR],
    [...curveSR, 0, ...lineSR],
    false,
    false,
    true
  );

  // Gradual decrease before wrist
  // Sts count before the last decrease before wrist rib band
  let lastSts = rounded(wristSts * 1.75, 2) + (baseStsCount % 2 === 1 ? 1 : 0);
  const decPairCount = (baseStsCount + padding.length * 2 - lastSts) / 2;
  const remainingRow = sleeveRow - 2;
  // Start with every 8 rows then 6 rows
  const { everyX, everyY, firstDecRow, secondDecRow } = getIncEveryXandYRows(
    remainingRow,
    decPairCount,
    8,
    6
  );

  const decFirstBaseChart = getDecEveryXRows(
    armBaseChart,
    sleeveSRChart.length,
    everyX,
    firstDecRow
  );
  rowCutID += everyX * firstDecRow;
  const decSecondBaseChart = getDecEveryXRows(
    armBaseChart,
    rowCutID,
    everyY,
    secondDecRow,
    everyX
  );
  rowCutID += decSecondBaseChart.length;
  const sleeveChart = getChartFromBasePattern({
    chart: [...decFirstBaseChart, ...decSecondBaseChart],
    motifList: sleeveMotifList,
    motifStartRowID: armPattern.chart.length - sleeveSRChart.length,
  });

  // Decrease 2 sts every 3 sts then 1 sts every 2 sts;
  const decSts = lastSts - wristSts;
  const decCountBy3 = Math.floor(decSts / 2);
  const hasDecBy2 = decSts - 2 * decCountBy3 > 0;

  const decCountBy2 = decSts - 2 * decCountBy3;
  const decBy2AfterX = Math.floor(decCountBy3 / (decCountBy2 + 1));
  const everyXSts = Math.floor(lastSts / (decCountBy3 + 1));
  // Decrease start stitch number, then decrease 2 or 1 sts at every X stitch
  let curDecSts = Math.round(everyXSts / 2);
  const lastDecRow = getRawChartFromBaseChart(
    armBaseChart.slice(rowCutID, rowCutID + 1),
    true
  )[0];
  const prevDecSts = everyX + everyY;
  const prevEmptySts = Array(prevDecSts).fill("empty");
  const remainedSts = lastDecRow.slice(prevDecSts, totalSts - prevDecSts);

  const decStsIDs = [];
  const decByList = Array.from({ length: decCountBy3 }).flatMap((_, i) => {
    if (i < decCountBy3 - 1 && (i + 1) % decBy2AfterX === 0) return [3, 2];
    return 3;
  });

  while (decStsIDs.length < decCountBy3 + 1) {
    const count = decStsIDs.length === decCountBy3 / 2 ? 2 : 3;
    remainedSts.splice(
      curDecSts,
      count,
      remainedSts[curDecSts] === "p"
        ? count === 2
          ? "p2tog"
          : "s2pp2"
        : count === 2
        ? "k2tog"
        : "s2kp2",
      ...Array(count - 1).fill("empty")
    );
    decStsIDs.push(curDecSts);
    curDecSts += everyXSts;
  }
  remainedSts.push(...prevEmptySts);
  remainedSts.unshift(...prevEmptySts);
  const lastIncChart = getChartFromRawChart([remainedSts]);

  return [...sleeveSRChart, ...sleeveChart, ...lastIncChart];
}
