import { getPattern as getHalfDiamondPattern } from "../../pattern/motifs/HalfDiamond";
import { getPattern as getBasicCablePattern } from "../../pattern/motifs/BasicCable";

import { MotifColor, StsID } from "../../utils/types";
import {
  getBaseSymmetricalPattern,
  getChartFromText,
  getBaseChartFromChart,
  getFlippedChart,
  getChartFromBasePattern,
} from "../../utils/calculator";

// Stitches in use
export const stitches: StsID[] = [
  "k",
  "p",
  "mb",
  "rli",
  "lli",
  "c21lp",
  "c21rp",
  "c6l",
  "c6r",
  "c4l",
  "c4r",
  "c2l",
  "c2r",
];

// Repeates motifs
const diamond = { ...getHalfDiamondPattern(5, 2), id: "pink" as MotifColor };
const cable2 = {
  ...getBasicCablePattern(2, "left", 2),
  id: "green" as MotifColor,
};
const cable4 = {
  ...getBasicCablePattern(4, "left", 6),
  id: "blue" as MotifColor,
};
export const motifs = [diamond, cable2, cable4];

const centerDiamond = getHalfDiamondPattern(5);
const centerDiamondFlipped = {
  ...centerDiamond,
  chart: getFlippedChart(centerDiamond.chart),
  id: "orange" as MotifColor,
};

const underArmChart = getChartFromText(["p k * 2", "k p * 2"]);
export const underArmBaseChartUnit = getBaseChartFromChart(underArmChart);

export const center = {
  chart: getChartFromText([
    "k:5 p k:5",
    "k:4 p:3 k:4",
    "k:3 p:2 k p:2 k:3",
    "k:2 p:2 k:3 p:2 k:2",
    "k:1 p:2 k:5 p:2 k:1",
    "k:2 p:2 k:3 p:2 k:2",
    "k:3 p:2 k p:2 k:3",
    "k:4 p:3 k:4",
  ]),
  stsCount: 11,
  id: "orange" as MotifColor,
};

const motifsInPattern = [diamond, cable2, cable4, cable2];
export const basePattern = getBaseSymmetricalPattern(
  motifsInPattern,
  "k:3",
  "p:2",
  center
);

const armCenter = getChartFromBasePattern(
  getBaseSymmetricalPattern([centerDiamondFlipped], null, null, {
    chart: getChartFromText(["k p k * 2", "p k p * 2"]),
    stsCount: 3,
  })
);

export const armPattern = getBaseSymmetricalPattern(
  [cable4, cable2, cable4, cable2],
  "p:2",
  "p:2",
  {
    chart: armCenter,
    id: "pink",
    stsCount: centerDiamondFlipped.stsCount * 2 + 3,
  }
);

// TODO: dynamic
export const totalRowCount = 103;
