import React from "react";
import { Profile } from "./utils/types";
import { css } from "@linaria/core";
import { unit } from "./shared/theme";

export const styles = {
  photo: css`
    width: ${unit * 40}px;
    height: ${unit * 60}px;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    border-radius: ${unit}px;
    margin-right: ${unit * 3}px;
  `,
};

export default function Intro({
  photos,
  sizes,
  yarn,
  needles,
  notions,
  guage,
  difficulty,
}: Profile) {
  return (
    <div>
      {photos.map((photo, i) => (
        <div
          key={i}
          className={styles.photo}
          style={{ backgroundImage: `url('/photos/${photo}')` }}
        />
      ))}
      <h3>🧵 Yarn</h3>
      <div>
        <a href={yarn.url} target="_parent">
          {yarn.line} - {yarn.color}
        </a>{" "}
        by {yarn.maker}
      </div>
      <h3>🧵 Sizes</h3>
      <div>
        {sizes.map((size, i) => (
          <div key={i}>
            {size.label}{" "}
            {Object.keys(size.work).map((d, j) => (
              <span key={j}>
                {d} - {size.work[d]}
              </span>
            ))}
            / {size.yarn_count}
            {` `}
            {yarn.shape}
            {size.yarn_count === 1 ? "" : "s"}
          </div>
        ))}
      </div>
      <h3>🪡 Needles</h3>
      <ul>
        {needles.map((needle, i) => (
          <li key={i}>
            {needle.size}, {needle.type}
          </li>
        ))}
      </ul>
      <h3>🧷 Notions</h3>
      <div>{notions}</div>
      <h3>📏 Guage</h3>
      <div>
        {guage.sts_count} sts and {guage.row_count} rows = 4" (10cm) in
        {guage.sts}, using {guage.needle}
      </div>
      <div>
        <i>{guage.notes}</i>
      </div>
      <h3>✨ Difficulty</h3>
      <div>{difficulty}</div>
    </div>
  );
}
