import { css, cx } from "@linaria/core";
import React, { useState } from "react";
import Nav from "./shared/Nav";
import { unit } from "./shared/theme";
import ChartComponent from "../pattern/Chart";
import { Chart, GlyphSize } from "../utils/types";
import WrittenPattern from "../pattern/WrittenPattern";

interface Props {
  chart: Chart;
  size?: GlyphSize;
  startRowID?: number;
}

const styles = {
  wrapper: css`
    display: flex;
    flex-wrap: wrap;
  `,
  chartWrapper: css`
    margin-right: ${unit * 2}px;
  `,
};

export default function PatternByParts({
  chart,
  size = "L",
  startRowID = 0,
}: Props) {
  const writtenPatternOptions = ["Inline", "Split by motif"];
  const [option, setOption] = useState<number>(0);
  return (
    <div className={cx(styles.wrapper)}>
      <ChartComponent
        chart={chart}
        className={styles.chartWrapper}
        direction="flat"
        size={size}
        startRowID={startRowID}
      />
      <div>
        <Nav
          menus={writtenPatternOptions}
          selectedMenu={option}
          setMenu={setOption}
        />
        <WrittenPattern
          chart={chart}
          direction="flat"
          startRowID={startRowID}
          display={option === 0 ? "inline" : "block"}
        />
      </div>
    </div>
  );
}
