import {
  StsID,
  Direction,
  Chart,
  Stitch,
  StsGroup,
  MotifColor,
  Motif,
  ChartRow,
  MOTIF_COLORS,
  RawChart,
  BasePattern,
  MotifInPattern,
  BaseChart,
} from "./types";
import stitchDefinition from "../stitches/definition";
const { k } = stitchDefinition;

export const isWSRow = (
  direction: Direction,
  rowID: number,
  ws?: "even" | "odd"
) => {
  if (direction === "round") return false;
  return rowID % 2 === (ws === "even" ? 0 : 1);
};

function getSameStitches(sameStsList: string): Stitch[] {
  return sameStsList.split(" ").flatMap((sameSts) => {
    const [stsID, count] = sameSts.trim().split(":");
    return Array(+(count ?? 1)).fill(stitchDefinition[stsID as StsID]);
  });
}

export function getStsGroup(stsRepText: string): StsGroup {
  const stitches = stsRepText.split(" ").flatMap((sameSts) => {
    return getSameStitches(sameSts);
  });
  return { sts: stitches };
}

export function getChartFromText(motifText: string[]): Chart {
  // MotifText array has multilpe items; each item is represents a row
  return motifText.flatMap((rowText) => {
    // Each row can be repeated by ' * n'
    const [singleRowText, rowRepeatCount] = rowText.split(" * ");
    // Stitch groups in a row
    const stsGroupText = singleRowText.trim().split(" ");
    const row = stsGroupText.map((sameSts) => {
      return { sts: getSameStitches(sameSts) };
    });
    return Array(+(rowRepeatCount ?? 1)).fill(row);
  });
}

export function getFlippedChart(
  chart: Chart,
  presetDirection?: "horizontal" | "vertical"
): Chart {
  const direction = presetDirection ?? "vertical";
  // Vertical flip - order of stitches in one row is reversed
  const flipped = chart.map((row, i) => {
    const rowFlipped = direction === "vertical" ? row.slice().reverse() : row;
    return rowFlipped.map((stsGroup: StsGroup) => {
      const flippedSts = stsGroup.sts.map((sts) => {
        const flippedSts = stitchDefinition[sts.stsID as StsID]?.flipped;
        return flippedSts != null ? stitchDefinition[flippedSts] : sts;
      });
      return { ...stsGroup, sts: flippedSts };
    });
  });
  // Horizontal flip - row numbers are reversed
  return direction === "horizontal" ? flipped.slice().reverse() : flipped;
}

export function getMotifStsGroup(
  chart: Chart,
  motifID: MotifColor,
  rowNum: number,
  stsCount: number
): StsGroup {
  return {
    sts: chart[rowNum]?.flatMap((stsGroup) => stsGroup.sts),
    motif: { id: motifID, rowNum, stsCount },
  };
}

function getRawStsFromRow(row: ChartRow): Stitch[] {
  return row.flatMap((stsGroup: StsGroup) => {
    return stsGroup.sts.flatMap((sts: Stitch) => {
      const { unitWidth = 1 } = sts;
      return [sts].concat(Array(unitWidth - 1).fill(null));
      // return sts;
    });
  });
}

export function getStsGroupText(
  stitches: Stitch[],
  isWS?: boolean
): { label: string; count: number }[] {
  const stsWithDir = isWS
    ? stitches
        .slice()
        .reverse()
        .map((sts: Stitch) => {
          const { stsID } = sts;
          const { ws } = stitchDefinition[stsID];
          return ws != null ? stitchDefinition[ws] : sts;
        })
    : stitches;

  const merged = stsWithDir.reduce(
    (acc: [StsID, number][], cur: Stitch, i: number): [StsID, number][] => {
      const { stsID } = cur;
      if (stsID !== "empty" && acc.length === 0) {
        acc.push([stsID, 1]);
      } else if (stsID !== "empty") {
        const [id, count] = acc[acc.length - 1];
        if (id === stsID && count > 0) {
          acc[acc.length - 1][1] = count + 1;
        } else {
          acc.push([stsID, 1]);
        }
      }
      return acc;
    },
    []
  );
  return merged.map((d) => {
    const [stsID, count] = d;
    const { label } = stitchDefinition[stsID];
    return { label, count };
  });
}

export function getStsCountText(
  sts: { label: string; count: number }[]
): string {
  const stsList = sts.map((d) => {
    const { label, count } = d;
    const repeatsLabel =
      count > 1 ? `${label === "k" || label === "p" ? "" : " x"}${count}` : "";
    return `${label}${repeatsLabel}`;
  });
  return stsList.join(", ");
}

export function getSymmetricalPattern(
  motifs: Motif[],
  edge: StsGroup,
  gutter: StsGroup,
  center: Motif,
  colors?: MotifColor[]
): Chart {
  const colorID =
    colors == null ? MOTIF_COLORS.slice(0, motifs.length + 1) : colors;
  const flippedChart = motifs.map((motif) => getFlippedChart(motif.chart));
  const maxRowCount = Math.max(
    ...motifs.map((motif) => motif.chart.length),
    center.chart.length
  );
  const chart = Array.from({ length: maxRowCount }).map((_, i) => {
    const row: ChartRow = [edge, { sts: edge.sts.slice().reverse() }];
    motifs.forEach((motif, j) => {
      // Add original motif with gutter
      row.splice(
        (j + 1) * 2 - 1,
        0,
        getMotifStsGroup(
          motif.chart,
          colorID[j],
          i % motif.chart.length,
          motif.stsCount
        ),
        gutter
      );
      // Add flipped motif with gutter
      row.splice(
        j * -2 - 1,
        0,
        gutter,
        getMotifStsGroup(
          flippedChart[j],
          colorID[j],
          i % motif.chart.length,
          motif.stsCount
        )
      );
    });
    // add center
    row.splice(
      Math.ceil(motifs.length * 2) + 1,
      0,
      getMotifStsGroup(
        center.chart,
        colorID[colorID.length - 1],
        i % center.chart.length,
        center.stsCount
      )
    );
    return row;
  });
  return chart;
}

export function getBaseSymmetricalPattern(
  motifs: Motif[],
  edge?: string | null,
  gutter?: string | null,
  center?: Motif,
  noCenterGutter: boolean = false
): BasePattern {
  // Make symmetrical motif list with color IDs and start Sts ID
  const gutterSts = gutter == null ? [] : getSameStitches(gutter);
  const edgeSts = edge == null ? [] : getSameStitches(edge);

  const allMotifs = [...motifs, ...motifs.slice().reverse()];
  if (center != null) {
    allMotifs.splice(motifs.length, 0, center);
  }
  const motifList = allMotifs.reduce((agg: MotifInPattern[], cur, i) => {
    const prevStartStsNum =
      agg[agg.length - 1]?.startStsNum ?? -gutterSts.length;
    const prevStsCount = agg[agg.length - 1]?.stsCount ?? edgeSts.length;
    const chart =
      i <= allMotifs.length / 2 ? cur.chart : getFlippedChart(cur.chart);
    const info = {
      ...cur,
      chart,
      startStsNum: prevStartStsNum + prevStsCount + gutterSts.length,
    };
    agg.push(info);
    return agg;
  }, []);

  const maxRowCount = Math.max(
    ...motifs.map((motif) => motif.chart.length),
    center?.chart.length ?? 0
  );

  const chart = Array.from({ length: maxRowCount }).map((_, i) => {
    const motifSts = motifList.flatMap((motif, j) => {
      return [
        ...getRawStsFromRow(motif.chart[i % motif.chart.length]),
        ...(j < motifList.length - 1 ? gutterSts : []),
      ];
    });
    return [...edgeSts, ...motifSts, ...edgeSts.slice().reverse()];
  });

  return {
    chart,
    motifList,
  };
}

export function getChartFromBasePattern({
  chart,
  motifList,
  motifStartRowID = 0,
}: BasePattern): Chart {
  return chart.map((row, i) => {
    const stsGroup = (start: number, end: number): StsGroup | null => {
      if (start >= end) return null;
      return {
        sts: row.slice(start, end).filter((d) => d != null) as Stitch[],
      };
    };
    const stsGroups: (StsGroup | null)[] = [];
    motifList.forEach((motif, j) => {
      const { startStsNum, stsCount, id } = motif;
      if (j === 0) {
        stsGroups.push(stsGroup(0, startStsNum));
      }
      stsGroups.push({
        ...stsGroup(startStsNum, startStsNum + stsCount),
        motif: {
          id: id as MotifColor,
          stsCount,
          // if motif has only one row, there's no concept of starting row,
          rowNum:
            motif.chart.length === 1
              ? null
              : (i - motifStartRowID) % motif.chart.length,
        },
      } as StsGroup);
      if (j < motifList.length - 1) {
        stsGroups.push(
          stsGroup(startStsNum + stsCount, motifList[j + 1].startStsNum)
        );
      }
      if (j === motifList.length - 1) {
        stsGroups.push(stsGroup(startStsNum + stsCount, row.length));
      }
    });
    return stsGroups.filter((d) => d != null) as StsGroup[];
  });
}

export function getRawSts(row: (StsID | null)[]): (StsID | null)[] {
  const rawSts = row.flatMap((stsID) => {
    if (stsID == null) return [];
    const { rawSts } = stitchDefinition[stsID];
    return rawSts ? rawSts : stsID;
  });
  return rawSts.slice(0, row.length);
}

export function getRawChartFromBaseChart(
  baseChart: BaseChart,
  useRawSts: boolean = false
): RawChart {
  return baseChart.map((row) =>
    row.flatMap((sts) => {
      if (sts == null) return [];
      const { stsID, unitWidth, rawSts } = sts;
      return useRawSts && rawSts
        ? rawSts
        : [stsID, ...Array((unitWidth ?? 1) - 1).fill(null)];
    })
  );
}

export function getRawStsFromMultiSts(
  stitches: (StsID | null)[],
  isSameStsCount: boolean = false
): StsID[] {
  const rawStitches: StsID[] = stitches.flatMap((stsID) => {
    if (stsID == null) return [];
    const { unitWidth, rawSts } = stitchDefinition[stsID];
    return rawSts ?? Array.from({ length: unitWidth ?? 1 }).map((_) => stsID);
  });
  return isSameStsCount ? rawStitches.slice(0, stitches.length) : rawStitches;
}

export function getBaseChartFromRawChart(rawChart: RawChart): BaseChart {
  return rawChart.map((row, i) =>
    row.map((stsID, j) => {
      if (stsID == null) return null;
      return stitchDefinition[stsID];
    })
  );
}

export function getSlicedBasePattern(
  basePattern: BasePattern,
  stsCut: [number, number],
  rowCut: [number, number],
  motifStartRowID: number = 0,
  kStsCount: number = 0
): BasePattern {
  const chart = basePattern.chart.slice(...rowCut).map((row) => {
    const sliced = row.slice(...stsCut);
    if (kStsCount < 0) {
      sliced.splice(kStsCount, -kStsCount, ...Array(-kStsCount).fill(k));
    } else if (kStsCount > 0) {
      sliced.splice(0, kStsCount, ...Array(kStsCount).fill(k));
    }
    return sliced;
  });
  const motifList = basePattern.motifList.flatMap((motif) => {
    const { startStsNum, stsCount } = motif;
    if (startStsNum < stsCut[0] || startStsNum + stsCount > stsCut[1]) {
      return [];
    }
    return { ...motif, startStsNum: startStsNum - stsCut[0] };
  });
  return { chart, motifList, motifStartRowID };
}

export function getBaseChartFromChart(chart: Chart): BaseChart {
  return chart.map((row) => row.flatMap((stsGroup) => stsGroup.sts));
}

export function getPushedMotifList(
  motifList: MotifInPattern[],
  stsCount: number
): MotifInPattern[] {
  return motifList.map((motif) => {
    return {
      ...motif,
      startStsNum: motif.startStsNum + stsCount,
    };
  });
}
