// Single stitch IDs
type BasicStsID = "k" | "p" | "mb" | "ktbl" | "koco";
type RibID = "k1p1" | "p1k1";
type CableID =
  | "c21lp"
  | "c21rp"
  | "c22lp"
  | "c22rp"
  | "c2l"
  | "c2r"
  | "c4l"
  | "c4r"
  | "c6l"
  | "c6r";
type IncID = "lli" | "rli";
type DecID = "k2tog" | "ssk" | "p2tog" | "ssp" | "s2kp2" | "s2pp2";
type NonStsID =
  | "empty"
  | "sr_rs"
  | "sr_ws"
  | "cy_rs"
  | "cy_ws"
  | "nc"
  | "ny_rs"
  | "ny_ws";
export type StsID = BasicStsID | RibID | CableID | IncID | DecID | NonStsID;

// Stitch definition
export interface Stitch {
  stsID: StsID;
  label: string;
  instruction: string;
  glyph?: React.ReactNode;
  unitWidth?: number;
  ws?: StsID;
  flipped?: StsID;
  isNonSts?: boolean;
  rawSts?: StsID[];
}

// Motif
export const MOTIF_COLORS = [
  "pink",
  "blue",
  "orange",
  "green",
  "grape",
  "red",
  "violet",
  "indigo",
  "lime",
  "cyan",
  "teal",
] as const;
type MotifColorTuple = typeof MOTIF_COLORS;
export type MotifColor = MotifColorTuple[number];
export interface Motif {
  title?: string;
  stsCount: number;
  chart: Chart;
  id?: MotifColor;
}
export interface MotifInPattern extends Motif {
  startStsNum: number;
}

// Chart
export type StsGroup = {
  sts: Stitch[];
  motif?: { id: MotifColor; rowNum: number | null; stsCount: number };
};
export type ChartRow = StsGroup[];
export type Chart = ChartRow[];

export type RawChart = (StsID | null)[][];

export type BaseChart = (Stitch | null)[][];

export interface BasePattern {
  chart: BaseChart;
  motifList: MotifInPattern[];
  motifStartRowID?: number;
}

export type Direction = "flat" | "round";
export type GlyphSize = "L" | "M" | "S";

export interface PatternOverview {
  notes: string[];
  parts: { direction?: Direction; instruction: string }[];
}
