import { Motif } from "../../utils/types";
import { getChartFromText } from "../../utils/calculator";

const TITLE = "Basic Braid";

export const getPattern = (stsCount: 4 | 8): Motif => {
  const rightCable = stsCount === 4 ? "c2r" : "c4r";
  const leftCable = stsCount === 4 ? "c2l" : "c4l";

  // row count is the same as sts count
  const chart = getChartFromText([
    `${rightCable} ${leftCable}`,
    `k:${stsCount} * ${stsCount / 4}`,
    `${leftCable} ${rightCable}`,
    `k:${stsCount} * ${stsCount / 4}`,
  ]);

  return {
    chart,
    stsCount,
    title: TITLE,
  };
};
