import React, { useState } from "react";
import { Direction, Motif } from "../utils/types";
import { css } from "@linaria/core";
import { unit } from "../projects/shared/theme";
import Chart, { FocusType } from "./Chart";
import Nav from "../projects/shared/Nav";
import WrittenPattern from "./WrittenPattern";

const styles = {
  wrapper: css`
    display: flex;
    margin-top: ${unit * 2}px;
  `,
  chartWrapper: css`
    margin-right: ${unit * 2}px;
  `,
};

export default function InteractiveMotif({ title, stsCount, chart }: Motif) {
  const [menu, setMenu] = useState<number>(0);
  const [focus, setFocus] = useState<FocusType>([null, null]);
  const direction: Direction = menu === 0 ? "round" : "flat";
  return (
    <div>
      <h2>{title}</h2>
      <Nav menus={["Round", "Flat"]} selectedMenu={menu} setMenu={setMenu} />
      <div>
        Repeat {stsCount} stitches x {chart.length}
        {direction === "round" ? " rounds" : " rows"}
      </div>
      <div className={styles.wrapper}>
        <Chart
          chart={chart}
          direction={direction}
          focus={focus}
          setFocus={setFocus}
          className={styles.chartWrapper}
        />
        <WrittenPattern chart={chart} direction={direction} focus={focus} />
      </div>
    </div>
  );
}
