import { Motif } from "../../utils/types";
import { getChartFromText } from "../../utils/calculator";

const STS_COUNT = 19;
const ROWS = 24;
const TITLE = "Moss Diamond";

export const getPattern = (): Motif => {
  const baseRowsText = Array.from({ length: ROWS / 2 - 2 }).map((_, i) => {
    const sideStsCount = Math.ceil(i / 2);
    const sideSts = sideStsCount > 0 ? `p:${sideStsCount}` : "";
    const stairStsR = i % 2 === 0 ? "c21lp" : "k:2";
    const stairStsL = i % 2 === 0 ? "c21rp" : "k:3";
    const midStsCount = STS_COUNT - sideStsCount * 2 - (i % 2 === 0 ? 6 : 5);
    const midSts = `k1p1:${Math.floor(midStsCount / 2)}`;
    return `${sideSts} ${stairStsR} ${midSts}${
      i % 2 === 0 ? " k " : " "
    }${stairStsL} ${sideSts}`;
  });
  const centerRowsText = [
    "p:2 mb p:2 k k1p1:3 k:2 p:2 mb p:2",
    "p:5 k k1p1:3 k:2 p:5",
  ];
  const baseRowsTextRev = Array.from({ length: ROWS / 2 - 3 }).map((_, i) => {
    const sideStsCount = Math.floor(STS_COUNT / 4) - Math.ceil(i / 2);
    const sideSts = sideStsCount > 0 ? `p:${sideStsCount}` : "";
    const stairStsR = i % 2 === 0 ? "c21rp" : "k:2";
    const stairStsL = i % 2 === 0 ? "c21lp" : "k:3";
    const midStsCount = STS_COUNT - sideStsCount * 2 - (i % 2 === 0 ? 6 : 5);
    const midSts = `k1p1:${Math.floor(midStsCount / 2)}`;
    return `${sideSts} ${stairStsR} ${midSts}${
      i % 2 === 0 ? " k " : " "
    }${stairStsL} ${sideSts}`;
  });
  const endRowsText = ["k k1p1:8 k:2", "k:2 k1p1:7 k:3", "k:2 k1p1:7 k:3"];

  const chart = [
    ...getChartFromText(baseRowsText),
    ...getChartFromText(centerRowsText),
    ...getChartFromText(baseRowsTextRev),
    ...getChartFromText(endRowsText),
  ];
  return {
    chart,
    stsCount: STS_COUNT,
    title: TITLE,
  };
};
