import React, { useState } from "react";
import { Link } from "react-router-dom";
import profile from "./profile";
import { stitches, motifs } from "./structure";
import Intro from "../Intro";
import StitchesInUse from "../StitchesInUse";
import RepeatedMotifs from "../RepeatedMotifs";
import Pattern from "./Pattern";
import Nav from "../shared/Nav";

const menus = [
  "Introduction",
  "Stitches in use",
  "Repeated motifs",
  "Pattern by parts",
];

export default function DiamondVest() {
  const [menu, setMenu] = useState<number>(0);
  return (
    <>
      <div>
        <Link to={`/knitting/`}>Back</Link>
      </div>
      <h1>{profile.name}</h1>
      <Nav menus={menus} selectedMenu={menu} setMenu={setMenu} />
      {menu === 0 && <Intro {...profile} />}
      {menu === 1 && <StitchesInUse stitches={stitches} />}
      {menu === 2 && <RepeatedMotifs motifs={motifs} />}
      {menu === 3 && <Pattern />}
    </>
  );
}
