import React, { useMemo, useState } from "react";
import SetInSleeveTopDownCardigan, {
  overview,
} from "../../pattern/garments/SetInSleeveTopDownCardigan";
import Nav from "../shared/Nav";
import InteractivePattern from "../../pattern/InteractivePattern";
import PatternByParts from "../PatternByParts";
import PatternWithMotifs from "../../pattern/PatternWithMotifs";
import { GarmentSize } from "../utils/types";
import profile from "./profile";
import sizeDetail from "./sizeDetail";
import {
  center,
  basePattern,
  patternBase,
  underArmBaseChartUnit,
} from "./structure";
import {
  getSetInSleeveCardigan,
  getSetInSleeveSize,
  getUnderArmBaseChart,
} from "../../pattern/calculator/setInSleeve";

export default function Pattern() {
  const { notes, parts } = overview;
  const menus: string[] = parts.map((_, i) => `Part ${i + 1}`);
  menus.unshift("Overview");

  const [menu, setMenu] = useState<number>(0);
  const selectedSize: GarmentSize = "One size";

  // Sts and Row count per inch
  const stsGuage = profile.guage.sts_count / 4;
  const rowGuage = profile.guage.row_count / 4;

  const size = getSetInSleeveSize(sizeDetail[selectedSize], stsGuage, rowGuage);

  const patternParts = useMemo(() => {
    // Front center sts overriden with k
    const centerKSts = Math.round(center.stsCount / 2);
    // sts count of each side of base pattern that is overridden with under arm stitch pattern
    const underArmMergeSts = 2;
    const underArmChart = getUnderArmBaseChart(
      underArmBaseChartUnit,
      "odd",
      size.sts.armInsetSts,
      underArmMergeSts
    );
    return getSetInSleeveCardigan(
      size,
      basePattern,
      underArmChart,
      underArmMergeSts,
      centerKSts
    );
  }, [size]);

  return (
    <div>
      <Nav menus={menus} selectedMenu={menu} setMenu={setMenu} />
      {menu === 0 && (
        <div>
          <ul>
            {notes.map((note, i) => (
              <li key={i}>{note}</li>
            ))}
          </ul>
          <SetInSleeveTopDownCardigan shrinkBy={1.5} />
          <ol>
            {parts.map((part, i) => (
              <li key={i}>{part.instruction}</li>
            ))}
          </ol>
          <h2>Pattern with Motif Repeats</h2>
          <PatternWithMotifs chart={patternBase} />
        </div>
      )}
      {menu === 1 && (
        <>
          <PatternByParts chart={patternParts[0]} />
          {/* <PatternByParts chart={one} /> */}
        </>
      )}
      {menu === 2 && (
        <>
          <PatternByParts chart={patternParts[1]} />
          {/* <PatternByParts chart={two} /> */}
        </>
      )}
      {menu === 3 && (
        <>
          <InteractivePattern chart={patternParts[2]} size="M" startRowID={6} />
          {/* <InteractivePattern chart={three} size="M" startRowID={one.length} /> */}
        </>
      )}
      {menu === 4 && (
        <>
          <PatternByParts chart={patternParts[3]} size="M" />
          {/* <PatternByParts chart={four} size="M" /> */}
        </>
      )}
      {menu === 5 && (
        <>
          <PatternByParts chart={patternParts[4]} size="M" />
          {/* <InteractivePattern chart={four} size="M" /> */}
        </>
      )}
      {menu === 6 && (
        <PatternByParts
          chart={patternParts[5]}
          size="S"
          startRowID={patternParts[4].length}
        />
      )}
    </div>
  );
}
