import React from "react";
import { Link } from "react-router-dom";

const patterns = [
  {
    name: "Diamond Vest",
  },
  {
    name: "Diamond Cardigan Old",
  },
  {
    name: "Diamond Cardigan",
  },
];

export default function App() {
  return (
    <div>
      <h1> knitting patterns by tanyoknits</h1>
      <div>
        {patterns.map((pattern, i) => (
          <h2 key={i}>
            <Link
              to={`/knitting/${pattern.name
                .toLowerCase()
                .split(" ")
                .join("-")}`}
            >
              {pattern.name}
            </Link>
          </h2>
        ))}
      </div>
      <div>Copyright(c) 2022. Tanyoung Kim. All Rights Reserved.</div>
    </div>
  );
}
