import React from "react";
import ReactDOM from "react-dom";
import App from "./App";
import reportWebVitals from "./reportWebVitals";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import DiamondVest from "./projects/diamond-vest";
import DiamondCardigan from "./projects/diamond-cardigan";
import DiamondCardiganOld from "./projects/diamond-cardigan-old";
import "./styles.css";

ReactDOM.render(
  <React.StrictMode>
    <BrowserRouter>
      <Routes>
        <Route path="/knitting/" element={<App />} />
        <Route path="/knitting/diamond-vest" element={<DiamondVest />} />
        <Route
          path="/knitting/diamond-cardigan"
          element={<DiamondCardigan />}
        />
        <Route
          path="/knitting/diamond-cardigan-old"
          element={<DiamondCardiganOld />}
        />
      </Routes>
    </BrowserRouter>
  </React.StrictMode>,
  document.getElementById("root")
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
