import { Motif, StsGroup } from "../../utils/types";
import stitchDefinition from "../../stitches/definition";
const { k, c2r, c2l, c4r, c4l, c6r, c6l } = stitchDefinition;

const TITLE = "Basic Cable";

export const getPattern = (
  stsCount: 6 | 4 | 2,
  orientation: "right" | "left",
  rowCount: 8 | 6 | 4 | 2
): Motif => {
  const bottomRowStsGroup: StsGroup = { sts: Array(stsCount).fill(k) };

  let cable = c6r;
  if (stsCount === 6 && orientation === "left") {
    cable = c6l;
  } else if (stsCount === 4 && orientation === "right") {
    cable = c4r;
  } else if (stsCount === 4 && orientation === "left") {
    cable = c4l;
  } else if (stsCount === 2 && orientation === "right") {
    cable = c2r;
  } else if (stsCount === 2 && orientation === "left") {
    cable = c2l;
  }
  const cableRow = [{ sts: [cable] }];
  return {
    chart: [
      Array(rowCount - 2).fill([bottomRowStsGroup]),
      [cableRow],
      [[bottomRowStsGroup]],
    ].flat(),
    stsCount,
    title: TITLE,
  };
};
