import { Motif } from "../../utils/types";
import { getFlippedChart, getChartFromText } from "../../utils/calculator";

const STS_COUNT = 12;
const TITLE = "Zig Zag";

const START_K_COUNT = 2;
const CENTER_P_COUNT = 3;
const END_K_COUNT = 7;

export const getPattern = (): Motif => {
  const baseRowsText = Array.from({ length: END_K_COUNT - 1 }).map((_, i) => {
    const startKCount = i + START_K_COUNT;
    return `k:${startKCount} p:${CENTER_P_COUNT} k:${
      STS_COUNT - CENTER_P_COUNT - startKCount
    }`;
  });
  const base = getChartFromText(baseRowsText);
  const baseFlipped = getFlippedChart(base, "horizontal");
  const chart = [...base, ...baseFlipped];

  return {
    chart,
    stsCount: STS_COUNT,
    title: TITLE,
  };
};
