import stitchDefinition from "../../stitches/definition";
import { getRawStsFromMultiSts } from "../../utils/calculator";
import {
  BaseChart,
  Chart,
  ChartRow,
  RawChart,
  StsGroup,
  StsID,
} from "../../utils/types";

const { empty } = stitchDefinition;

export const rounded = (value: number, roundBy?: number): number => {
  return Math.round(value / (roundBy ?? 1)) * (roundBy ?? 1);
};

/** Sitch & Row count */
export const countFromGuage = (
  size: number,
  guage: number,
  roudBy?: number
): number => {
  return rounded(size * guage, roudBy);
};

export const stsFromRow = (
  rowCount: number,
  stsGuage: number,
  rowGuage: number
): number => {
  const rowLength = rowCount / rowGuage;
  return rounded(rowLength * stsGuage);
};
/** simultaneous equation
every a rows x times
every b rows y times
ax + by = row
x + y = sts
a > b, all even numbers
*/
export function getIncEveryXandYRows(
  rowCount: number,
  stsCount: number,
  firstDecRow: number,
  secDecRow: number
): {
  everyX: number;
  everyY: number;
  firstDecRow: number;
  secondDecRow: number;
} {
  // assuming a > b;
  let a = firstDecRow;
  let b = secDecRow;
  function getDecCount(
    a: number,
    b: number
  ): {
    everyX: number;
    everyY: number;
    firstDecRow: number;
    secondDecRow: number;
  } {
    const everyX = Math.round((rowCount - b * stsCount) / (a - b));
    const everyY = stsCount - everyX;
    return { everyX, everyY, firstDecRow: a, secondDecRow: b };
  }
  const { everyX, everyY } = getDecCount(a, b);
  if (everyX < 0 && b > 2) {
    // If the bigger number is negative, reduce row numbers
    return getDecCount(a - 2, b - 2);
  } else if (everyY < 0) {
    // If the smaller number is negative, ignore and reduce every X
    return {
      everyX: everyX + everyY,
      everyY: 0,
      firstDecRow: a,
      secondDecRow: b,
    };
  } else {
    return { everyX, everyY, firstDecRow: a, secondDecRow: b };
  }
}

/** Convert between Chart and RawChart (list of StsIDs) */

export function getRawChart(patternBase: Chart): RawChart {
  return patternBase.map((chartRow) =>
    chartRow.flatMap((stsGroup) =>
      stsGroup.sts.flatMap((sts) => {
        const { stsID, unitWidth, rawSts } = sts;
        return rawSts ?? [stsID, ...Array((unitWidth ?? 1) - 1).fill(null)];
      })
    )
  );
}

export function getStsGroupFromStsIDs(rawChartRow: (StsID | null)[]): StsGroup {
  return {
    sts: rawChartRow.flatMap((sts) =>
      sts == null ? [] : [stitchDefinition[sts]]
    ),
  };
}

export function getChartFromRawChart(rawChart: RawChart): Chart {
  return rawChart.map((row): ChartRow => {
    const stsGroup = getStsGroupFromStsIDs(row);
    return [stsGroup];
  });
}

/** Other chart manipulation */
export function getEmptyStsPaddedChart(
  chart: Chart,
  emptyCount: number
): Chart {
  // Empty stitch before and after the base pattern before increase
  const emptyStsGroup = { sts: Array(emptyCount).fill(empty) };
  return chart.map((row) => [emptyStsGroup, ...row, emptyStsGroup]);
}

export function getFullLengthBaseChart(
  baseChart: BaseChart,
  rowCount: number,
  rowID: number = 0,
  chart: BaseChart = []
): BaseChart {
  let lastRowID = rowID;
  while (chart.length < rowCount - baseChart.length) {
    chart.push(...baseChart.slice(lastRowID));
    lastRowID = 0;
  }
  lastRowID = rowCount - chart.length;
  chart.push(...baseChart.slice(0, lastRowID));
  return chart;
}

export function getRawStsForReplace(
  row: (StsID | null)[],
  startID: number,
  endID: number
): StsID[] {
  const noNullSts: StsID[] = row.flatMap((stsID) => {
    if (stsID == null) return [];
    const { unitWidth, rawSts } = stitchDefinition[stsID];
    return rawSts ?? Array.from({ length: unitWidth ?? 1 }).map((_) => stsID);
  });
  return noNullSts.slice(startID, endID);
}

export function getValidRawSts(
  row: (StsID | null)[],
  startID: number,
  endID: number
): (StsID | null)[] {
  const rawRow = getRawStsFromMultiSts(row);

  // If valid stitch starts with null, replace it with the raw sts
  let nullID = startID;
  while (row[nullID] == null) {
    row.splice(nullID, 1, rawRow[nullID]);
    nullID++;
  }
  // If multi-width stitch ends in the middle of valid sts, replace it with raw sts
  nullID = endID;
  while (row[nullID] == null) {
    nullID--;
  }
  if (nullID < endID) {
    row.splice(nullID, endID - nullID, ...rawRow.slice(nullID, endID));
  }
  return row;
}
