import React, { useState } from "react";
import ChartComponent, { FocusType } from "./Chart";
import { Chart, Direction, GlyphSize } from "../utils/types";

type Props = {
  chart: Chart;
  size?: GlyphSize;
  direction?: Direction;
  startRowID?: number;
};

export default function InteractivePattern({
  chart,
  size = "M",
  startRowID = 0,
}: Props) {
  const [focus, setFocus] = useState<FocusType>([0, null]);
  return (
    <ChartComponent
      chart={chart}
      direction="flat"
      size={size}
      startRowID={startRowID}
      focus={focus}
      setFocus={setFocus}
      isOnlyRowClickable={true}
    />
  );
}
