/** Short row calculation */

import stitchDefinition from "../../stitches/definition";
import {
  getBaseChartFromRawChart,
  getRawChartFromBaseChart,
  getRawStsFromMultiSts,
} from "../../utils/calculator";
import { BasePattern, Chart, RawChart, Stitch, StsID } from "../../utils/types";
import { getChartFromBasePattern } from "../../utils/calculator";
import { getRawStsForReplace, getValidRawSts } from "./common";

/** Ellipse equation 
  x^2 / a^2 + y^2 / b^2 = 1; x axis: sts, y axis: row, a: stsCount, b: rowCount
  Returns sts count from 0,0 every other row
  */
export function getCurveSRCount(stsCount: number, rowCount: number): number[] {
  const curveStsCount: number[] = [];
  let sum = 0;
  for (let row = 1; row < rowCount; row += 2) {
    const sts = Math.round(
      Math.sqrt(
        (1 - (row * row) / (rowCount * rowCount)) * (stsCount * stsCount)
      )
    );
    curveStsCount.push(sts - sum);
    sum += stsCount - sts;
  }
  return curveStsCount;
}

/** Line
    Get the start count, then increase equally till the last row
    Returns sts count from the end, every other row
    */
export function getLineSRCount(stsCount: number, rowCount: number): number[] {
  const start = Math.round((stsCount / rowCount) * 2);
  const inc = Math.ceil((stsCount - start) / ((rowCount - 2) / 2));
  const lineStsCount: number[] = [];
  let sum = start;
  for (let row = 1; row < rowCount; row += 2) {
    lineStsCount.push(Math.max(stsCount - sum, 0)); // last row may have fewer inc
    sum += inc;
  }
  return lineStsCount;
}

function getSROutlineRawChart(
  stsCount: number,
  rowCount: number,
  startSR: number[],
  endSR: number[],
  // Use CO instead of Wrap & Turn
  isStartCO: boolean = false,
  isEndCO: boolean = false,
  hasPickUpSts: boolean = false
): RawChart {
  const kChart = Array.from({ length: rowCount }).map((_) =>
    Array.from({ length: stsCount }).map((_) => "k" as StsID)
  );

  const startEmptyCount = (i: number) =>
    Math.max(startSR[Math.ceil(i / 2)] ?? 0, 0);
  const endEmptyCount = (i: number) =>
    Math.max(endSR[Math.floor(i / 2)] ?? 0, 0);

  // Start empty stitch with SR symbol
  const getStartEmptySts = (currentCount: number, i: number) => {
    const prevCount = startEmptyCount(i - 1);
    if (
      currentCount > 0 &&
      i % 2 === 1 && // right side
      prevCount !== currentCount &&
      !isStartCO
    ) {
      return [...Array(currentCount - 1).fill("empty"), "sr_rs"];
    } else {
      return Array(currentCount).fill("empty");
    }
  };

  // End empty stitch with SR symbol
  const getEndEmptySts = (currentCount: number, i: number) => {
    const prevCount = endEmptyCount(i - 1);
    if (
      i > 0 &&
      currentCount > 0 &&
      i % 2 === 0 && // wrong side
      prevCount !== currentCount &&
      !isEndCO
    ) {
      return ["sr_ws", ...Array(currentCount - 1).fill("empty")];
    } else {
      return Array(currentCount).fill("empty");
    }
  };

  const kocoSts = (count: number) => {
    return Array(count).fill("koco");
  };
  // Manipulate each row with short row
  for (let i = 0; i < kChart.length; i++) {
    const sCount = startEmptyCount(i);
    const startEmptySts = getStartEmptySts(sCount, i);
    const eCount = endEmptyCount(i);
    const endEmptySts = getEndEmptySts(eCount, i);

    // Replace with empty sts
    kChart[i].splice(0, sCount, ...startEmptySts);
    kChart[i].splice(-eCount, eCount, ...endEmptySts);

    // Replace with CO for increase instead of SR
    if (isStartCO && i % 2 === 1) {
      // ending at wrong side
      const coSts = startEmptyCount(i - 1) - sCount;
      kChart[i].splice(sCount, coSts, ...kocoSts(coSts));
    }
    if (isEndCO && i > 0 && i % 2 === 0) {
      // ending at right side
      const coSts = endEmptyCount(i - 1) - eCount;
      kChart[i].splice(-eCount - coSts, coSts, ...kocoSts(coSts));
    }

    // Short row start in the middle - when there's no initial pick up stitch
    // ending at wrong side
    const prevSCount = startEmptyCount(i - 1);
    const nextTwoSCount = startEmptyCount(i + 2);
    if (
      !hasPickUpSts &&
      i % 2 === 1 &&
      sCount === 0 &&
      prevSCount > 0 &&
      nextTwoSCount > 0
    ) {
      kChart[i].splice(0, prevSCount, ...kocoSts(prevSCount));
    }
    // ending at right side
    const prevECount = endEmptyCount(i - 1);
    const nextTwoECount = endEmptyCount(i + 2);
    if (
      !hasPickUpSts &&
      i % 2 === 0 &&
      eCount === 0 &&
      prevECount > 0 &&
      nextTwoECount > 0
    ) {
      kChart[i].splice(-prevECount, prevECount, ...kocoSts(prevECount));
    }
  }
  return kChart;
}

export function getSRChart(
  basePattern: BasePattern,
  stsCount: number,
  rowStartID: number,
  rowCount: number,
  startSR: number[],
  endSR: number[],
  // Use CO instead of Wrap & Turn
  isStartCO: boolean = false,
  isEndCO: boolean = false,
  hasPickUpSts: boolean = false
): Chart {
  const kChart = getSROutlineRawChart(
    Math.abs(stsCount),
    rowCount,
    startSR,
    endSR,
    isStartCO,
    isEndCO,
    hasPickUpSts
  );

  // Raw chart from base chart with motif
  const rawChart: RawChart = getRawChartFromBaseChart(
    basePattern.chart.slice(rowStartID, rowStartID + rowCount)
  ).map((row) => {
    if (stsCount > 0) return row.slice(0, stsCount);
    else return row.slice(stsCount);
  });

  // Raw chart with sittch info and short row shapes
  const srChart: RawChart = [];
  kChart.forEach((row, i) => {
    // Knit stitches between empty/co/sr stitches
    const kStart = row.indexOf("k");
    const kEnd = row.lastIndexOf("k");

    // Replace prev row sts with the short-rowed sts
    let prev = 2;
    while (i - prev >= 0) {
      const prevStart = kChart[i - prev][0];
      const prevEnd = kChart[i - prev][kChart[i - prev].length - 1];
      if (kStart > 0 && prevStart === "k") {
        const replaced = getRawStsForReplace(rawChart[i], 0, kStart);
        srChart[i - prev].splice(0, kStart, ...replaced);
      }
      const endEmptyCount = row.length - kEnd;
      if (endEmptyCount > 0 && prevEnd === "k") {
        const replaced = getRawStsForReplace(
          rawChart[i],
          Math.abs(stsCount) - endEmptyCount,
          Math.abs(stsCount)
        );
        srChart[i - prev].splice(-endEmptyCount, endEmptyCount, ...replaced);
      }
      prev += 2;
    }

    // Manipulate valid stitches between empty stitches
    const rawRow = getRawStsFromMultiSts(rawChart[i]);
    // Start motifs like cables from the row 3.
    if (i < 2) {
      rawChart[i].splice(kStart, kEnd + 1, ...rawRow.slice(kStart, kEnd + 1));
    } else {
      const validSts = getValidRawSts(rawChart[i], kStart, kEnd + 1);
      rawChart[i] = validSts;
    }

    const srRow = [
      ...row.slice(0, kStart),
      ...rawChart[i].slice(kStart, kEnd + 1),
      ...row.slice(kEnd + 1, row.length),
    ];
    srChart.push(srRow);
  });

  const srBaseChart = getBaseChartFromRawChart(srChart);

  // Adjust motif start number
  const motifList = basePattern.motifList.flatMap((motif) => {
    const startStsNum =
      stsCount > 0
        ? motif.startStsNum
        : motif.startStsNum - (basePattern.chart[0].length + stsCount);
    // Remove motif if it's out of the sliced chart
    if (startStsNum < 0 || startStsNum + motif.stsCount >= srChart[0].length)
      return [];
    return {
      ...motif,
      startStsNum,
    };
  });

  return getChartFromBasePattern({ chart: srBaseChart, motifList });
}

