import { getPattern as getHalfDiamondPattern } from "../../pattern/motifs/HalfDiamond";
import { getPattern as getBasicCablePattern } from "../../pattern/motifs/BasicCable";
import { getPattern as getHoneyCombPattern } from "../../pattern/motifs/HoneyComb";
import { StsID } from "../../utils/types";
import {
  getStsGroup,
  getSymmetricalPattern,
  getBaseSymmetricalPattern,
  getChartFromText,
  getBaseChartFromChart,
} from "../../utils/calculator";

// Stitches in use
export const stitches: StsID[] = [
  "k",
  "p",
  "mb",
  "rli",
  "lli",
  "c21lp",
  "c21rp",
  "c6l",
  "c6r",
  "c4l",
  "c4r",
  "c2l",
  "c2r",
];

// Repeates motifs
const motifA = getHalfDiamondPattern(5, 3);
const motifB = getBasicCablePattern(6, "left", 8);
const motifC = getBasicCablePattern(2, "left", 2);
const motifD = getBasicCablePattern(4, "left", 6);
export const center = getHoneyCombPattern(4);
export const motifs = [motifA, motifB, motifC, motifD, center];

export const basePattern = getBaseSymmetricalPattern(
  [
    { ...motifA, id: "pink" },
    { ...motifB, id: "blue" },
    { ...motifC, id: "orange" },
    { ...motifD, id: "green" },
  ],
  "k:2",
  "p",
  { ...center, id: "grape" }
);

// Pattern base
const edge = getStsGroup("k:2");
const gutter = getStsGroup("p:1");

export const patternBase = getSymmetricalPattern(
  [motifA, motifB, motifC, motifD],
  edge,
  gutter,
  center
);

const underArmChart = getChartFromText(["p k * 2", "k p * 2"]);
export const underArmBaseChartUnit = getBaseChartFromChart(underArmChart);

// TODO: dynamic
export const totalRowCount = 103;
