import { Motif } from "../../utils/types";
import { getChartFromText } from "../../utils/calculator";

const BASE_STS_COUNT = 8;
const TITLE = "Half Diamond";

export const getPattern = (
  stepCount: 3 | 5 | 7 = 5,
  padding: 0 | 1 | 2 | 3 | 4 = 0,
  hasMB: boolean = true
): Motif => {
  const firstRowText = `p k1p1:${padding + Math.floor(stepCount / 2)} k:3`;
  const startChart = getChartFromText([firstRowText, firstRowText]);

  const slashText: string[] = Array.from({ length: stepCount }).flatMap(
    (_, i) => {
      const inside = padding + Math.floor((stepCount - i) / 2);
      const isEvery2 = i % 2 === 0; // rib change
      const center = (isRS: boolean): string => {
        const outside = i + (isRS ? 0 : 1);
        return isEvery2
          ? `k1p1:${inside} ${isRS ? `k c21rp` : `k:3`}${
              outside > 0 ? ` p:${outside}` : ""
            }`
          : `p k1p1:${inside - 1} k ${isRS ? `c21rp` : `k:2`}${
              outside > 0 ? ` p:${outside}` : ""
            }`;
      };
      return [center(true), center(false)];
    }
  );

  const centerRowText = `p1k1:${padding + 1} k p:${stepCount}`;
  const centerChart = getChartFromText([
    hasMB ? `p k1p1:${padding} k:2 p:${stepCount - 2} mb p` : centerRowText,
    centerRowText,
  ]);

  const backSlashText: string[] = Array.from({ length: stepCount }).flatMap(
    (_, i) => {
      const inside = padding + Math.floor((i + 1) / 2);
      const isEvery2 = i % 2 === 0; // rib change
      const center = (isRS: boolean): string => {
        const outside = stepCount - i - 1;
        return isEvery2
          ? `k1p1:${inside} ${isRS ? `k c21lp` : `k:4`}${
              outside > 0 ? ` p:${outside}` : ""
            }`
          : `p k1p1:${inside - 1} k ${isRS ? `c21lp` : `k:3`}${
              outside > 0 ? ` p:${outside}` : ""
            }`;
      };
      return [center(true), center(false)];
    }
  );

  const lastRow = getChartFromText([
    `k1p1:${padding + Math.round(stepCount / 2)} k:2`,
  ]);

  const chart = [
    ...startChart,
    ...getChartFromText(slashText),
    ...centerChart,
    ...getChartFromText(backSlashText.slice(0, backSlashText.length - 1)),
    ...lastRow,
  ];

  return {
    chart,
    stsCount: BASE_STS_COUNT + padding * 2,
    title: TITLE,
  };
};
