import React from "react";
import { StsID } from "../utils/types";
import stitchDefinition from "../stitches/definition";
import StitchSymbol from "../stitches/StitchSymbol";
import { css, cx } from "@linaria/core";
import { themeBySize } from "../styles/theme";
import { unit } from "./shared/theme";

type Props = {
  stitches: StsID[];
};

const styles = {
  stitch: css`
    display: flex;
    align-items: center;
    &:not(:last-child) {
      margin-bottom: ${unit * 2}px;
    }
  `,
  symbol: css`
    margin-right: ${unit}px;
    border: 1px solid var(--oc-gray-8);
  `,
};
export default function StitchesInUse({ stitches }: Props) {
  return (
    <div className={cx(themeBySize.L)}>
      {stitches.map((stsID, i) => {
        const sts = stitchDefinition[stsID];
        const { label, ws, instruction } = sts;
        const wsSts = ws != null ? stitchDefinition[ws] : null;
        return (
          <div key={i} className={styles.stitch}>
            <StitchSymbol sts={sts} className={styles.symbol} />
            <div>
              {`${ws != null ? "RS: " : ""}`}
              {instruction} ({label})
            </div>
            {ws != null && (
              <div>
                , WS: {instruction} ({wsSts?.label})
              </div>
            )}
          </div>
        );
      })}
    </div>
  );
}
